import React, { Component } from "react"
import ScrollAnimation from "react-animate-on-scroll"
import "./Technologies.css";

//ICONS
import javascript from "../icons/JavaScript.svg"
import html5 from "../icons/html5.svg"
import css from "../icons/css-5.svg"
import bootstrap from "../icons/bootstrap-5.svg"
import java from "../icons/java-4.svg"
import c from "../icons/c.svg"
import unreal from "../icons/unreal-1.svg"
import react from "../icons/react.svg"
import firebase from "../icons/firebase-1.svg"
import meteor from "../icons/meteor-5.svg"
import mongo from "../icons/mongodb.svg"
import npm from "../icons/npm.svg"
import sql from "../icons/sql.svg"


class Technologies extends Component {
  render() {
    return (
      <div id="technologies">
        <ScrollAnimation animateIn="fadeIn">
          <img className="techIcons" src={javascript} />
          <img className="techIcons" src={html5} />
          <img className="techIcons" src={css} />
          <img className="techIcons" src={bootstrap} />
          <img className="techIcons" src={java} />
          <img className="techIcons" src={c} />
          <img className="techIcons" src={unreal} />
          <img className="techIcons" src={react} />
          <img className="techIcons" src={firebase} />
          <img className="techIcons" src={meteor} />
          <img className="techIcons" src={mongo} />
          <img className="techIcons" src={npm} />
          <img className="techIcons" src={sql} />
        </ScrollAnimation>
      </div>
    )
  }
}

export default Technologies
